          <template v-for="question in questions">
            <v-stepper-step
              :key="`${question.id}-content`"
              :complete="e1 > question.id"
              :step="question.id"
            >Step {{ question.id }}</v-stepper-step>
            <v-divider v-if="question.id !== steps" :key="question.id"></v-divider>
          </template>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content
            v-for="question in questions"
            :key="`${question.id}-content`"
            :step="question.id"
          >
            <v-card class="mx-auto">
              <v-card-title class="headerCard">{{question.name}}</v-card-title>
              <v-row class="justify-content justify-left">
                <v-col cols="12" class="d-flex align-center">
                  <v-img :src="question.src" :height="question.height"></v-img>
                </v-col>
              </v-row>

              <v-row>
                <v-col class="ml-5">
                  <v-radio-group v-model="radios" :mandatory="false">
                    <v-card-subtitle :hidden="!complete" class="mb-0">Select a response:</v-card-subtitle>
                    <v-radio
                      v-for="response in question.responses"
                      :key="response"
                      :label="response"
                      :value="response+'-1'"
                      class="mt-3"
                    ></v-radio>
                    <!-- <v-radio :label="question.responses.b" :value="question.id+'-2'" class="mt-3"></v-radio>
                    <v-radio :label="question.responses.c" :value="question.id+'-3'" class="mt-3"></v-radio>
                    <v-radio :label="question.responses.d" :value="question.id+'-4'" class="mt-3"></v-radio>-->
                  </v-radio-group>
                </v-col>
              </v-row>
              <v-progress-linear :value="progress"></v-progress-linear>
              <v-card-actions class="d-flex justify-space-between mb-6 mt-6">
                <v-btn color="primary" :disabled="!complete" @click="nextStep(question.id)">Continue</v-btn>
              </v-card-actions>
            </v-card>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
