          height: "377px",
          question: `
                import { assertStrictEquals } from '../../testing/asserts.ts';
                // Copyright 2018-2020 the Deno authors. All rights reserved. MIT license.

                Deno.test('[examples/_______] print a _______ message', async () => {
                    const decoder = new TextDecoder();
                    const process = Deno.run({
                    cmd: [Deno.execPath(), 'run', '_______.ts'],
                    cwd: 'examples',
                    stdout: 'piped',
                });
                    try {
                        const output = await process.output();
                        const actual = decoder.decode(output).trim();
                        const expected = '_______ __ ____ ðŸ¦•';
                        assertStrictEquals(actual, expected);
                    } finally {
                        process.close();
                    }
                });`,
          responses: {
            a: "welcome, welcome, welcome, Welcome to Deno",
            b: "welcome, welcome, welcome, Welcome to Deninho",
            c: "Hello, hello, welcome, Welcome to Node",
            d: "PHP, PHP, PHP, Using to PHP"
          }
        },
        {
          id: 6,
          name: "Result",
          level: "FÃ¡cil",
          src: require("../assets/Q5.png"),
          height: "100%",
          question: "",
          responses: {}
        }
      ],
      steps: 0
    };
  },

  beforeMount() {
    this.steps = this.questions.length;
    this.complete = true;
  },

  watch: {
    steps(val) {
      if (this.e1 > val) {
        this.e1 = val;
      }
      if (this.e1 == this.steps - 1) {
        this.complete = false;
      }
    }
  },

  methods: {
    nextStep(n) {
      if (n === this.steps) {
        this.complete = false;
      } else {
        this.e1 = n + 1;
      }
      this.progress = n * 20;
    }
  }
};
</script>

